@use '../../../styles/mixins';

.basket {
  position: relative;

  display: flex;
  justify-content: center;
  align-items: center;

  width: 40px;
  height: 40px;

  background-color: #46B37A;
  color: #fff;
  box-shadow: 0 1px 2px rgb(0 0 0 / 0%);

  @include mixins.mq(tablet) {
    display: flex;
    gap: 10px;
    width: 140px;
    height: 50px;
  }
}

.basketCount {
  position: absolute;
  top: 0;
  right: 3px;

  font-size: 10px;
}

.priceLay {
  display: flex;
  align-items: center;
}
